* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
ol,
ul,
li,
dl,
dt,
dd {
  list-style: none;
}
body {
}
.inner {
  width: 1200px;
  margin: auto;
}
header {
  background-color: white;
  width: 100%;
  padding: 2rem 0;
  border-bottom: 3px solid royalblue;
}
header > .inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.gnb {
  width: 600px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.gnb > a {
  text-decoration: none;
  color: gray;
  transition: 550ms;
  font-weight: 600;
  font-size: 1.3rem;
}
.gnb > a:hover {
  color: #020715;
}
.main_wrap {
  display: flex;
  column-gap: 3rem;
  margin-top: 2rem;
}
.banner {
  flex: 0.7;
  width: 50%;
  overflow: hidden;
  box-shadow: 3px 3px 3px gray;
  border-radius: 90px;
  background-color: #fff;
}
.banner > img {
  width: 100%;
  height: 100%;
}
.main_wrap > aside {
  flex: 0.3;
}
.tab_menu {
  padding: 1rem;
  background-color: #fff;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.tab_btn {
  border: none;
  border-bottom: 3px solid transparent;
  outline: none;
  margin: 0 1rem;
  cursor: pointer;
  font-size: 2rem;
  font-weight: 600;
  background-color: white;
  transition: 550ms;
}
.tab_btn.active {
  border-bottom: 3px solid royalblue;
}
.tab_btn:hover {
  border-bottom: 3px solid blue;
}
.gonji {
  background-color: #fff;
}
.gonji > ul {
  padding-bottom: 2rem;
}
.gonji > ul > li {
  padding: 1rem;
}
.gallery {
  background-color: #fff;
  margin-bottom: 2rem;
}
.gallery > ul {
}
.gallery > ul > li {
  padding: 1rem;
}
#notice {
  margin-bottom: 3rem;
  border-radius: 45px;
  overflow: hidden;
  box-shadow: 3px 3px 5px lightgray;
}
#dwn {
  position: relative;
  padding: 3rem 1rem;
  border-radius: 45px;
  background-color: #fff;
  box-shadow: 3px 3px 5px lightgray;
  overflow: hidden;
}
#dwn::before {
  content: "";
  display: block;
  position: absolute;
  width: 100px;
  height: 100px;
  border-radius: 100%;
  background-color: rgba(80, 171, 236, 0.5);
  left: 170px;
  top: -40px;
}
